.app {
  font-family: sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.container {
  display: flex;
  gap: 10px;
  width: 100%;
  max-width: 1200px;
}

.tour-info {
  width: 60%;
}

.tour-title {
  margin-bottom: 20px;
}

.rating {
  font-size: 1.2em;
  color: #4caf50;
}

.details {
  font-size: 0.9em;
  color: #666;
}

.tour-highlights,
.tour-overview {
  margin-bottom: 20px;
}

.tour-highlights ul,
.tour-overview ul {
  list-style: disc;
  padding-left: 20px;
}

.booking-section {
  width: 40%;
  background-color: #f8f8f8;
  padding: 20px;
  border-radius: 8px;
}

.price {
  text-align: center;
  margin-bottom: 20px;
}

.original-price {
  font-size: 0.9em;
  color: #999;
  text-decoration: line-through;
}

.discounted-price {
  font-size: 2em;
  color: #4caf50;
  font-weight: bold;
}

.discount {
  font-size: 0.8em;
  color: #4caf50;
}

.booking-form {
  margin-bottom: 20px;
}

.booking-form label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.booking-form input,
.booking-form textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 10px;
}

.booking-form button {
  background-color: #4caf50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: 100%;
}

.privacy-policy {
  margin-top: 20px;
}

.privacy-policy ul {
  list-style: none;
  padding: 0;
}

.privacy-policy li {
  margin-bottom: 5px;
}


.list-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

h1 {
  font-size: 2em;
  margin-bottom: 20px;
}

.boarding-points {
  /* display: flex; */
  /* flex-direction: column; */
  width: 100%;
  border: 1px solid #ccc;
  padding: 10px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  background-color: #f2f2f2;
}

.header span {
  font-weight: bold;
}

.boarding-point {
  /* display: flex; */
  justify-content: space-between;
  align-items: center;
  padding: 2px;
}

input[type="text"],
input[type="date"] {
  padding: 8px;
  margin-right: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 150px;
}

.delete-button {
  padding: 8px 15px;
  background-color: #f44336;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.add-button {
  padding: 10px 20px;
  background-color: #f0ad4e;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 15px;
}

.calendar-icon {
  font-size: 1.5em;
}


/* CheckboxList.css */
.checkbox-list {
  /* display: flex;
    flex-direction: row;
    flex-wrap: nowrap; */
  /* gap: 5px; Space between checkboxes */
  /* padding: 10px; */

  display: flex;
  flex-wrap: wrap; /* Allow items to wrap to the next row */
  gap: 10px; /* Space between items */
  padding: 10px;
  background-color: #ffffff;
  border: 1px solid #ffffff;
  box-sizing: border-box;
}

.checkbox-item {
  display: flex;
  align-items: center;
  gap: 2px;
  /* Space between checkbox and label */
  /* padding: 20%; */
}


.Inclusion-container {
  display: flex;
  flex-wrap: wrap; /* Allow items to wrap to the next row */
  gap: 10px; /* Space between items */
  padding: 10px;

  
}

.icon-image{
  height: 100px;
  width: 300px;
}