.grid-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
    width: 100%;
    margin: 0 auto;
  }
  
  .grid-item {
    text-align: center;
  }
  
  .image {
    width: 100%;
    height: auto;
  }
  
  .upload-button-container {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
  }
  
  .upload-button {
    padding: 8px 16px;
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .add-button {
    padding: 8px 16px;
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 4px;
    margin: 0 auto;
    display: block;
    width: fit-content;
  }
  
  .title {
    text-align: center;
  }