.image-carousel {
  position: relative;
  width: 75vw ;/* adjust the width to your needs */
  height: 600px ;/* adjust the height to your needs */
  margin: 40px auto;
  border-radius: 5px;
}

.carousel-inner {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 5px;
}

.carousel-item {
  position: relative; /* changed from absolute to relative */
  width: 100%;
  height: 100%;
  display: none;
}

.carousel-item.active {
  display: block;
}

.carousel-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.upload-button {
  background-color: #f8b15f;
  color: #fff;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 10px;
  transition: background-color 0.3s ease;
}

.upload-button:hover {
  background-color: #f7d8ac;
}

.carousel-prev,
.carousel-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 36px;
  cursor: pointer;
  z-index: 1;
}

.carousel-prev {
  left: 20px;
}

.carousel-next {
  right: 20px;
}