* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: Arial, sans-serif;
    line-height: 1.6;
    color: #333;
    background-color: #ffffff;
}

.container {
    max-width: 1200px;
    margin: 40px auto;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

/* Left container styles */

.left-container {
    flex-basis: 50%;
    margin: 20px;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* Right container styles */

.right-container {
    flex-basis: 40%;
    margin: 20px;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* Tour card styles */

.tour-card {
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.tour-card h2 {
    margin-bottom: 10px;
}

.rating {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.rating span {
    margin-right: 10px;
}

.rating span:first-child {
    font-size: 24px;
    font-weight: bold;
    color: #4CAF50;
}

.details {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.details span {
    margin-right: 20px;
}

.icons {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.icon {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.icon img {
    width: 50px;
    height: 50px;
    margin-bottom: 5px;
}

.tour-card h3 {
    margin-bottom: 10px;
}

.tour-card ul {
    list-style: disc;
    padding-left: 20px;
}

.tour-card ul li {
    margin-bottom: 5px;
}

/* Price card styles */

.price-card {
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
}

.price-card p {
    margin-bottom: 5px;
}

.price-card h2 {
    margin-bottom: 10px;
}

.price-card span {
    font-weight: bold;
    color: #FF5733;
}

/* Form styles */

form {
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.form-group {
    margin-bottom: 10px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
}

.form-group input,
.form-group textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.form-group textarea {
    height: 100px;
}

button[type="submit"] {
    background-color: #FF5733;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button[type="submit"]:hover {
    background-color: #E65100;
}

.privacy-notice {
    margin-top: 20px;
}

.privacy-notice ul {
    list-style: none;
    padding: 0;
}

.privacy-notice ul li {
    margin-bottom: 5px;
}

/* Responsive styles */

@media (max-width: 992px) {
    .left-container {
        flex-basis: 60%;
    }

    .right-container {
        flex-basis: 40%;
    }
}

@media (max-width: 768px) {
    .left-container {
        flex-basis: 80%;
    }

    .right-container {
        flex-basis: 60%;
    }
}

@media (max-width: 480px) {
    .left-container {
        flex-basis: 100%;
    }

    .right-container {
        flex-basis: 100%;
    }
}

.carousel {
    position: relative;
    overflow: hidden;
}

.carousel-inner {
    display: flex;
    transition: transform 0.5s ease-in-out;
}

.carousel-item {
    flex: 0 0 100%;
    width: 100%;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.carousel-item img {
    max-width: 100%;
    max-height: 100%;
}

.carousel-item.active {
    position: relative;
    opacity: 1;
    z-index: 1;
}

.carousel-item:not(.active) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
}